.no-arrivals {
    text-align: center;
    margin: 0px;
    line-height: 50px;
}

.spin-icon-wrapper {
    width: 100%;
    height:100%;
    display: inline-block;
    padding: 20px;
}

.arrival-load-spinner {
    text-align: center;
    line-height: 50px;
    width: 100%;
}

.arrivals-table {
    font-size: 14px;
}
.route-indicator-column {
    padding-top: 0px;
    padding-bottom: 0px;
    width: 30px;
}

.arrivals-table th, .arrivals-table td {
    padding: 5px;
    text-align: left;
}

.arrival-on-time {
    color: green;
}
.arrivals-loading {
    opacity: 0.3;
}

.arrivals-wrapper {
    position: relative;
}

.arrivals-load-more-control {
    background: lightgrey;
    padding: 2px;
}

.arrivals-table thead {
    background: lightgrey;
}

.load-more-flex-container {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.load-more-flex-container button {
    max-width: 50%;
    background: darkgrey;
    font-size: 14px;
    line-height: 26px;
    cursor: pointer;
}

.load-more-flex-container button:hover {
    background-color: dimgray;
}

.arrival-estimated-late {
    color: red;
}

.arrival-estimated-early {
    color: yellowgreen;
}

.route-indicator {
  background: #1e4d88 !important;
  height: 21px;
  min-width: 18px;
  vertical-align: middle;
  text-align: center;
  color: white !important;
  font-size: 12px;
  line-height: 22px;
  border-radius: 5px;
  display: inline-block;
  cursor: pointer;
  border: 2px solid #c25033;
  padding-left: 2px;
  padding-right: 2px; }

.train-route-indicator {
  margin-right: 5px; }

.route-indicator-green {
  color: black;
  background: green !important;
  border: 2px solid green !important; }

.route-indicator-yellow {
  color: black;
  background: yellow !important;
  border: 2px solid yellow !important; }

.route-indicator-red {
  color: black;
  background: red !important;
  border: 2px solid red !important; }

.route-indicator-blue {
  color: white;
  background: blue !important;
  border: 2px solid blue !important; }

.route-indicator-orange {
  color: black;
  background: orange !important;
  border: 2px solid orange !important; }

.route-indicator-lightgreen {
  color: white;
  background: #99C354 !important;
  border: 2px solid #99C354 !important; }

.route-indicator-pink {
  color: white;
  background: #cc3e8b !important;
  border: 2px solid #cc3e8b !important; }

.route-indicator-cyan {
  color: white;
  background: #3D8FAE !important;
  border: 2px solid #3D8FAE !important; }

.bookmark-button {
    background: rgb(0,0,0, 0.5);
    display: inline-block;
    padding-left: 5px;
    padding-right: 5px;
    cursor: pointer;
    vertical-align: middle;
    line-height: 25px;
    text-align: center;
    width: 30px;
    font-size: 16px;
    border: none;
    border-radius: 5px 5px 5px 5px;
}

.bookmark-button:hover {
    background: rgb(0,0,0, 0.7);
}

.bookmark-button-container {
    line-height: 20px;
}

.stop-location-indicator {
    background: darkgrey;
    height: 21px;
    min-width: 40px;
    vertical-align: middle;
    text-align: center;
    color: black;
    font-size: 16px;
    line-height: 22px;
    border-radius: 5px;
    display: inline-block;
    cursor: pointer;
    padding-left: 4px;
    padding-right: 4px;
    margin-right: 10px;
}

.header-router-indicator {
    margin-right: 5px;
}

.stop-info-header {
    -webkit-flex: 4 1;
            flex: 4 1
}

.serving-label {
    line-height: 22px;
    font-size: 14px;
}

.route-detail-list {
    margin: 0;
    padding: 0;
    list-style: none;
}

.route-detail-list-item {
    font-size: 14px;
    line-height: 30px;
}

.route-description-divider {
    font-weight: bold;
    padding-left: 5px;
    padding-right: 5px;
}

.stops-header {
    border-radius: 5px 5px 0px 0px;
    line-height: 35px;
    padding-left: 10px;
    padding-right: 10px;
    position: relative;
    display: -webkit-flex;
    display: flex;
    padding-top: 5px;
    padding-bottom: 5px;
}

.stops-header.arrivals-hidden {
    border-radius: 5px 5px 5px 5px;
}

.stops-header h2 {
    margin: 0px;
    font-size: 16px;
    line-height: 22px;
}

